<template>
  <div class="dashboard-container">
    <div class="searchTitle">
      <el-input placeholder="请输入商品编码" v-model="code"></el-input>
      <el-select placeholder="请选择物料类别" v-model="category">
        <el-option
          :label="'测试'"
          :value="'测试'">
        </el-option>
      </el-select>
      <el-select placeholder="请选择商品状态" v-model="state">
        <el-option
          :label="'测试'"
          :value="'测试'">
        </el-option>
      </el-select>
      <el-select placeholder="请选择品牌" v-model="brand">
        <el-option
          :label="'测试'"
          :value="'测试'">
        </el-option>
      </el-select>
      <el-select placeholder="请选择生产厂家" v-model="factory">
        <el-option
          :label="'测试'"
          :value="'测试'">
        </el-option>
      </el-select>
      <el-select placeholder="请选择产地" v-model="area">
        <el-option
          :label="'测试'"
          :value="'测试'">
        </el-option>
      </el-select>
      <el-select placeholder="输入保质期数" v-model="days">
        <el-option
          :label="'测试'"
          :value="'测试'">
        </el-option>
      </el-select>
      <el-button type="primary" class="search-btn">搜索</el-button>
    </div>
    <div class="line"></div>
    <div class="goods-form">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="goodsCode"
          label="商品编码"
          width="120">
        </el-table-column>
        <el-table-column
          prop="nationCode"
          label="国际条码"
          width="120">
        </el-table-column>
        <el-table-column
          prop="approval"
          label="批准文号"
          width="120">
        </el-table-column>
        <el-table-column
          prop="categoryName"
          label="品名全称"
          width="120">
        </el-table-column>
        <el-table-column
          prop="materialCategory"
          label="物料类型"
          width="120">
        </el-table-column>
        <el-table-column
          prop="brand"
          label="品牌"
          width="120">
        </el-table-column>
        <el-table-column
          prop="size"
          label="主规格"
          width="120">
        </el-table-column>
        <el-table-column
          prop="unit"
          label="主单位"
          width="120">
        </el-table-column>
        <el-table-column
          prop="price"
          label="零售价"
          width="120">
        </el-table-column>
        <el-table-column
          prop="factory"
          label="生产厂家"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="area"
          label="产地"
          width="120">
        </el-table-column>
        <el-table-column
          prop="time"
          label="保质期"
          width="120">
        </el-table-column>
        <el-table-column
          prop="specialCategory"
          label="特殊类别"
          fixed="right"
          width="120">
          <template slot-scope="scope">
            <el-button size="mini" type="danger">{{scope.row.specialCategory}}</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="goodsState"
          label="商品状态"
          fixed="right"
          width="120">
          <template slot-scope="scope">
            <el-button size="mini" type="primary">{{scope.row.goodsState}}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="margin-top: 20px">
        <el-button @click="toggleSelection([tableData[1], tableData[2]])">切换第二、第三行的选中状态</el-button>
        <el-button @click="toggleSelection()">取消选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Dashboard",
        data() {
            return {
                code: '',
                category: '',
                state: '',
                brand: '',
                factory: '',
                area: '',
                days: '',
                tableData: [{
                    goodsCode: '01010204001',
                    nationCode: 'A12A020301',
                    approval: ' 国药准字H14022744',
                    categoryName: '阿司匹林肠溶片',
                    materialCategory: '药品-西药-非处方药',
                    brand: '仁和',
                    size: '50mg*25片*3板',
                    unit: '盒',
                    price: '¥ 29.90',
                    factory: ' 大同市利群药业有限公司',
                    area: '山西省大同市湖东经济开发区',
                    time: '24月',
                    specialCategory: '麻醉药品',
                    goodsState: '有效'
                }, {
                    goodsCode: '01010204001',
                    nationCode: 'A12A020301',
                    approval: ' 国药准字H14022744',
                    categoryName: '阿司匹林肠溶片',
                    materialCategory: '药品-西药-非处方药',
                    brand: '仁和',
                    size: '50mg*25片*3板',
                    unit: '盒',
                    price: '¥ 29.90',
                    factory: ' 大同市利群药业有限公司',
                    area: '山西省大同市湖东经济开发区',
                    time: '24月',
                    specialCategory: '精神药品',
                    goodsState: '已冻结'
                }, {
                    goodsCode: '01010204001',
                    nationCode: 'A12A020301',
                    approval: ' 国药准字H14022744',
                    categoryName: '阿司匹林肠溶片',
                    materialCategory: '药品-西药-非处方药',
                    brand: '仁和',
                    size: '50mg*25片*3板',
                    unit: '盒',
                    price: '¥ 29.90',
                    factory: ' 大同市利群药业有限公司',
                    area: '山西省大同市湖东经济开发区',
                    time: '24月',
                    specialCategory: '毒性药品',
                    goodsState: '停止销售'
                }, {
                    goodsCode: '01010204001',
                    nationCode: 'A12A020301',
                    approval: ' 国药准字H14022744',
                    categoryName: '阿司匹林肠溶片',
                    materialCategory: '药品-西药-非处方药',
                    brand: '仁和',
                    size: '50mg*25片*3板',
                    unit: '盒',
                    price: '¥ 29.90',
                    factory: ' 大同市利群药业有限公司',
                    area: '山西省大同市湖东经济开发区',
                    time: '24月',
                    specialCategory: '放射药品',
                    goodsState: '停止采购'
                }],
                multipleSelection: []
            }
        },
        methods: {
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }
</script>

<style lang="scss" scoped>
  .dashboard {
    &-container {
      display: flex;
      flex-direction: column;
      min-height: calc(100vh - 130px);
      background-color: #ffffff;

      .searchTitle {
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 10px;

        .el-input {
          width: auto;
        }

        .el-input, .el-select {
          margin-top: 10px;
          margin-left: 10px;
          border: 1px solid #DDDDDD;
        }

        .search-btn {
          margin-top: 10px;
          margin-left: 10px;
          height: 40px;
          padding: 0 50px;
        }
      }

      .line {
        height: 10px;
        background-color: #DDDDDD;
      }
      .goods-form{
        padding: 10px 10px;
      }
    }

    &-text {
      font-size: 30px;
      line-height: 46px;
    }
  }
</style>



